    @media #{$md}{
        min-height: 300px;
        
    }
    @media #{$sm}{
        min-height: 300px;
    }
    @media #{$xs}{
        min-height: 300px;
    }
}
.slider-height2{
    min-height: 250px;
    background-position: center center;
    background-size: cover;
    @media #{$md}{
        min-height: 250px;
    }
    @media #{$sm}{
        min-height: 250px;
    }
    @media #{$xs}{
