// Hero Overly
.hero-overly {
    position: relative;
    z-index: 0;
    &::before {
        position: absolute;
        content: "";
        // background-color: #000000B5;
        background-color: rgba(6, 27, 40, 0.8);
        // @extend %overlay1;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: -1;
        background-repeat: no-repeat;
    }
}

@keyframes action-bg {
    from {
        background-position: 0 0;
    }
    to {
        background-position: -1080px 0;
    }
}

.hero-bg{
    min-height: 1100px;
    width: 100%;
    background-image: url("../img/gallery/hero.jpg");
    color: #ffffff;
    position: relative;
    text-align: center;
    animation: action-bg 30s linear infinite;
    background-position: 0 0;
    background-repeat: repeat-x;
    z-index: 1;
    @media #{$xs-to-lg-max}{
        min-height: 680px;
    }
}

// Main Slider
.sliderArea{
    // Slider Caption
    .slider-content {
        overflow: hidden;
        &.slider-content{
            .title{
                color: var(--white);
                font-size: 40px;
                font-weight: 700;
                line-height: 1.2;
                margin-bottom: 25px;
            @media #{$xs}{
                    font-size: 25px;
            }
            }
            .pera{
                font-size: 18px;
                font-weight: 500;
                color: var(--white);
                padding: 0;
                margin: 0;
            }
        }
        &.slider-content2{
            .title{
                color: var(--white);
                font-size: 40px;
                font-weight: 700;
                line-height: 1.2;
                margin-bottom: 20px;
                font-family: var(--extra-font);
                @media #{$xs}{
                    font-size: 30px;
                }
            }
            .pera{
                font-size: 18px;
                font-weight: 500;
                color: var(--white);
                padding: 0;
                margin: 0;
                @media #{$xs}{
                    font-size: 15px;
                }
            }
        }
    }
}


// breadcrumb
.breadcrumb {
    background: none;
    padding: 0;
    margin: 0;
    justify-content: center;
    margin-bottom: 5px;
    a {
        color: var(--heading-color-one);
        font-size: 18px;
        text-transform: capitalize;
        line-height: 1;
        font-weight: 400;
    }
    .breadcrumb-item{
        line-height: 1;
    }
    .breadcrumb-item + .breadcrumb-item{
        &::before {
            display: inline-block;
            color: var(--heading-color-one);
            content: "\e649";
            font-family: 'themify';
            font-size: 16px;
            padding: 0 0px;
            margin: 0 13px;
            font-weight: 600;
            position: relative;
            top: 3px;
            padding-right: 10px;
        }
    }    
}


.tags span {
	background-color: #e0eaf7;
	color: var(--primary);
	display: inline-block;
	margin: 0 5px 10px 0;
	padding: 5px 15px;
	border-radius: 30px;
	font-size: 14px;
	transition: 0.3s;
}